import counter from '../../../components/counter.vue'
export default {
	components:{
		counter
	},
	data() {
		return {
		  footbottom: 0,	
		  goodsList: [],
		  theIndex: null,//控制滑动效果，当前滑动下标
		  oldIndex: null,//上一个左滑下标
		  selectedList: [],
		  isAllselect: false,
		  sumPrice: '0.00'
		}
	},
	// 刚进页面从存储中取到的数据
	onShow() {
		uni.getStorage({
			key:"goodsList",
			success:(res => {
				console.log(res.data)
	             //刚进页面把商品设置成未选中状态
				for (let i=0;i<res.data.length;i++) {
					res.data[i].selected = false
				}
				this.goodsList = res.data
				//属性数据的初始化
				
				this.selectedList = []
				this.sumPrice = '0.00' 
				this.isAllselect = false
			})
		})
	},
	methods: {
		//复选框的选择
		handleCheckbox(item) {
		   item.selected =!item.selected
		   // 条件: 数组中是否包含该元素
		   let isExest = this.selectedList.indexOf(item)
		   if(isExest > -1) {
			   this.selectedList.splice(isExest,1);
		   } else {
		   // push到数组里
		   this.selectedList.push(item)
		  }
		  //判断全选状态
		  if (this.selectedList.length ==this.goodsList.length) {
			  this.isAllselect = true
		  } else {
			  this.isAllselect = false
		  }
		  this.sum()//合计
		},
		//全选按钮
		handleSelectAll() {
			this.isAllselect =!this.isAllselect
			//数据处理
			let arr = []
			this.goodsList.forEach((item,index) => {
				item.selected = this.isAllselect
				arr.push(item)
			})
			this.selectedList = this.isAllselect ? arr:[]
			this.sum()
		},
		//合计
		sum() {
			this.sumPrice = 0
			this.goodsList.forEach((item,index) => {
				if(item.selected) {
					this.sumPrice = this.sumPrice + (item.number * item.price)
				}
			})
			this.sumPrice = this.sumPrice.toFixed(2)
		},
		handleGoodsinfo(item) {
			uni.navigateTo({
				url:"../../goods/goods?goodsInfo=" + JSON.stringify(item)
			})
		},
		handlesp(index,event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}
			
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			
			// 初始化坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		handlemove(index,event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}
			
			// 移动位置
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			// 滑动位置小 不触发
			if (Math.abs(moveX) < 5) {
				return;
			}
			
			// 竖向滑动 不触发
			if (Math.abs(moveY) > Math.abs(moveX)) {
				return;
			}
			
			// 左滑
			if (moveX < 0) {
				this.theIndex = index;
			}
		},
		handleend(index,event) {
			
		},
	},
	onLoad() {
		// 兼容h5的底部结算



	}
}
